"use client";

import React, { useState, useEffect } from "react";
import questionsData from "../data/questions"; // 📌 Ajusta la ruta según sea necesario
import ExamQuestion from "./ExamQuestion";

interface ExamModalProps {
  isOpen: boolean;
  onClose: () => void;
  moduleId: number;
}

const ExamModal: React.FC<ExamModalProps> = ({ isOpen, onClose, moduleId }) => {
  if (!isOpen) return null;

  const [questions, setQuestions] = useState([]);
  const [currentQuestion, setCurrentQuestion] = useState(0);
  const [answers, setAnswers] = useState<{ [key: number]: string }>({});
  const [score, setScore] = useState<number | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    console.log("📌 Buscando preguntas para moduleId:", moduleId);
    console.log("📌 Datos de questionsData:", questionsData);

    if (moduleId && questionsData[moduleId]) {
      setQuestions(questionsData[moduleId]);
      setLoading(false);
    } else {
      console.error(`❌ No se encontraron preguntas para el módulo ${moduleId}`);
      setQuestions([]); // 🔥 Evita que `questions` sea undefined
      setLoading(false);
    }
  }, [moduleId]);

  const handleAnswer = (selectedOption: string) => {
    setAnswers((prev) => ({
      ...prev,
      [currentQuestion]: selectedOption,
    }));
  };

  const handleNext = () => {
    if (!answers[currentQuestion]) {
      alert("Por favor, selecciona una respuesta.");
      return;
    }
    if (currentQuestion < questions.length - 1) {
      setCurrentQuestion(currentQuestion + 1);
    } else {
      handleSubmit();
    }
  };

  const handlePrevious = () => {
    if (currentQuestion > 0) {
      setCurrentQuestion(currentQuestion - 1);
    }
  };

  const handleSubmit = () => {
    let correctCount = 0;
    questions.forEach((question, index) => {
      if (answers[index] === question.correctAnswer) {
        correctCount += 1;
      }
    });

    const finalScore = (correctCount / questions.length) * 10;
    setScore(finalScore);
  };

  return (
    <div className="fixed inset-0 bg-black bg-opacity-90 flex items-center justify-center p-6">
      <div className="bg-white p-6 rounded-lg shadow-lg w-full max-w-3xl h-full overflow-auto relative">
        
        <button onClick={onClose} className="absolute top-4 right-4 text-gray-500 text-lg">✖</button>

        <div className="relative w-full bg-gray-200 h-2 rounded-full mt-4">
          <div
            className="bg-pink-500 h-2 rounded-full transition-all"
            style={{ width: `${((currentQuestion + 1) / questions.length) * 100}%` }}
          ></div>
        </div>

        <h1 className="text-2xl font-bold text-center mt-4">Desafío de Conocimiento</h1>

        {loading ? (
          <p className="text-center text-gray-500">Cargando preguntas...</p>
        ) : questions.length === 0 ? (
          <p className="text-center text-red-500">⚠️ No hay preguntas disponibles para este módulo.</p>
        ) : (
          <>
            <p className="text-center text-gray-600">{questions[currentQuestion]?.title || "Pregunta sin título"}</p>
            <p className="text-right text-gray-500">Pregunta {currentQuestion + 1} de {questions.length}</p>

            <ExamQuestion
              index={currentQuestion}
              question={questions[currentQuestion]}
              selectedAnswer={answers[currentQuestion] || ""}
              onSelectAnswer={handleAnswer}
            />

            <div className="flex justify-between mt-6">
              <button onClick={handlePrevious} disabled={currentQuestion === 0} className="px-4 py-2 border rounded">
                Anterior Pregunta
              </button>
              <button onClick={handleNext} className="px-4 py-2 bg-pink-500 text-white rounded">
                {currentQuestion === questions.length - 1 ? "Finalizar" : "Siguiente"}
              </button>
            </div>
          </>
        )}
      </div>
    </div>
  );
};

export default ExamModal;
  