import Link from "next/link";

interface ExamButtonProps {
  isDisabled: boolean;
  moduleId: number;
  children: React.ReactNode;
}

const ExamButton: React.FC<ExamButtonProps> = ({ isDisabled, moduleId, children }) => {
  return (
    <Link href={`/exams/${moduleId}`} passHref legacyBehavior>
      <button
        disabled={isDisabled}
        className={`button--primary ${
          isDisabled ? "cursor-not-allowed opacity-50" : ""
        }`}
      >
        {children}
      </button>
    </Link>
  );
};

export default ExamButton;
